import { memo, useState } from 'react';
import useElementOnScreen from '../util/IntersectionObserver/useElementOnScreen'
import PureCountsItem from './PureCountsItem';
import './style.css'

const PureCounts = () => {
    const [containerRef, isVisible] = useElementOnScreen()
    const [pureCountData, setPureCountData] = useState([
        { title: 'MAKTABLAR', end: 50 },
        { title: 'O`QUVCHILAR', end: 22390 },
        { title: 'O`QITUVCHILAR', end: 2007 },
        { title: 'TILDA TA\'LIM OLIB BORILADI', end: 5 }
    ])

    return (
        <section id="counts" className="counts section-bg">
            <div className="container">
                <div
                    className="row justify-content-end"
                    ref={containerRef}
                >
                    {
                        pureCountData?.map((data, index) => (
                            <PureCountsItem
                                key={index + '.' + data.end}
                                title={data.title}
                                isVisible={isVisible}
                                end={data.end}
                            />
                        ))
                    }
                </div>
            </div>
        </section>
    )
}

export default memo(PureCounts)